import React from 'react'
import ReactDOM from 'react-dom/client'
import App from './App.jsx'
import './index.css'

console.log('Iniciando aplicao...')

// Garantir que o elemento root existe
const rootElement = document.getElementById('root')
if (!rootElement) {
  console.error('Elemento root no encontrado!')
  throw new Error('Elemento root no encontrado')
}

console.log('Elemento root encontrado, renderizando aplicao...')

try {
  ReactDOM.createRoot(rootElement).render(
    <React.StrictMode>
      <App />
    </React.StrictMode>,
  )
  console.log('Aplicao renderizada com sucesso!')
} catch (error) {
  console.error('Erro ao renderizar aplicao:', error)
}